/* src/App.css */
.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #121212;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  color: #e0e0e0;
  position: relative;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #1e1e1e;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
  padding: 20px;
  max-width: 400px;
  width: 100%;
}

.game-board {
  margin-bottom: 20px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  background: #2b2b2b;
  border: 1px solid #3d3d3d;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 50px;
  height: 50px;
  width: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
  color: #e0e0e0;
  transition: background-color 0.2s;
}

.square:hover {
  background-color: #3d3d3d;
}

.square:focus {
  outline: none;
}

.game-info {
  width: 100%;
  text-align: left;
}

.game-info > div {
  margin-bottom: 10px;
  font-size: 18px;
  color: #e0e0e0;
}

.game-info ol {
  padding-left: 20px;
}

.game-info button {
  background: #007aff;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  margin-bottom: 5px;
  padding: 5px 10px;
  transition: background-color 0.2s;
}

.game-info button:hover {
  background-color: #005bb5;
}

@media (max-width: 600px) {
  .square {
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
  }

  .game {
    padding: 10px;
  }

  .game-info > div {
    font-size: 16px;
  }

  .game-info button {
    font-size: 12px;
  }
}

.winner {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #007aff;
  color: white;
  font-size: 24px;
  font-weight: bold;
  padding: 20px;
  border-radius: 10px;
  margin-top: 20px;
  position: relative;
  width: 100px;
  height: 100px;
  animation: winner-animation 1s ease-in-out;
}

.letter-n {
  font-size: 48px;
  color: #007aff;
  position: relative;
  z-index: 10;
}

.circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 5px solid #007aff;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

@keyframes winner-animation {
  0% { transform: scale(0.5); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.animation-container {
  position: absolute;
  bottom: 10px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ball {
  width: 20px;
  height: 20px;
  background-color: #007aff;
  border-radius: 50%;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-30px); }
  60% { transform: translateY(-15px); }
}

