/* src/components/EndAnimation.css */
.end-animation {
  position: relative;
  width: 200px;
  height: 200px;
}

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
  border: 2px solid #007aff;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

.letter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 72px;
  font-weight: bold;
  color: #007aff;
}
